<script>
import MiniCard from "../components/MiniCard.vue";
import ItemCard from "../components/ItemCard.vue";
import Tabs from "../components/tabs.vue"
import { mdbCarousel, mdbBtn, mdbCard, mdbCardBody, } from "mdbvue";

export default {
  data() {
    return {
      carousel: [
        {
          img: true,
          src: require("../assets/11/1-2.jpg"),
        },
        {
          img: true,
          src: require("../assets/11/carousel-3.jpg"),
        },
        {
          img: true,
          src: require("../assets/11/3-1.jpg"),
        },
      ],
      newProducts: [],
      randomProducts: [this.items[16]],
      fourRandom: [],
      topSellers: [this.items[20], this.items[24], this.items[21]],
      act: 1,
    };
  },
  props: {
    items: [],
  },
  components: {
    mdbCarousel,
    MiniCard,
    ItemCard,
    mdbBtn,
    Tabs,
    mdbCard, mdbCardBody,
  },
  mounted() {
    this.fiveProducts();
    this.fourProducts();
    this.act = 0;
  },
  methods: {
    fourProducts() {
      let temp = [...this.items];
      let products = [];
      for (let i = 0; i < 4; i++) {
        let n = Math.floor(Math.random() * temp.length);
        products.push(temp[n]);
        temp.slice(n, 1);
      }
      this.fourRandom = products;
    },
    fiveProducts() {
      let temp = [...this.items];
      let products = [];
      for (let i = 0; i < 5; i++) {
        let n = Math.floor(Math.random() * temp.length);
        if (i >= 3) {
          this.randomProducts.push(temp[n]);
          temp.slice(n, 1);
        } else {
          products.push(temp[n]);
          temp.slice(n, 1);
        }
      }
      this.newProducts = products;
    },
    slice(text) {
      let sliced = text.slice(0,40) + '...'
      return sliced
    }
  },
};
</script>

