<template>
  <div class="page-loader" v-if="!isloaded">
<div class="content">
   <div class="loading">
<p>Cargando..</p>
      <span></span>
   </div>
</div>
  </div> 
</template>

<script>
  export default {
    data: () => {
      return {
        isloaded: false
      }
    },
    mounted() {
      document.onreadystatechange = () => {
        if (document.readyState == "complete") { 
          this.isloaded = true;
        } 
      }
    },
  }
</script>

